.title h1 {
    font-size: 56px;
    font-weight: 700;
    color: #fff;
    text-transform: uppercase;
    line-height: 1.2;
    margin: 30px 0 30px;
    position: relative;
}

.title::after {
    content: '';
    width: 55px;
    height: 1px;
    background: white;
    position: absolute;
    transform: translate(-50%, 50%);
}
