@import "./common/styles/sass/variables";

* {
  box-sizing: border-box;
  outline: none;
  padding: 0;
  margin: 0;
}

::before,
::after {
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  font-family: 'Poppins', sans-serif;
  font-size: 16px;
  font-weight: 300;
  color: $blue-dark;
  line-height: 1.5;
  overflow-x: hidden;
  padding: 5vh 15px;
  background-image: linear-gradient(to bottom right, #{ $pink-light }, #{ $cyan-light });
  -webkit-tap-highlight-color: transparent;

  &::before {
    content: '';
    position: fixed;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    background-color: $green-yellow;
    z-index: -1;
    opacity: 0.12;
  }
}
